<template>
  <div>
    <div class="d-block header-section">
      <h1>
        <b>{{title}}</b>
      </h1>
      <b-col class="mb-2 col-md-6 float-right search-section">
        <b-form-group
          label
          label-cols-sm="3"
          label-align-sm="right"
          label-size="sm"
          label-for="filterInput"
          class="mb-0"
        >
          <b-input-group>
            <b-form-input
              v-model="filter"
              class="form-control"
              type="search"
              id="filterInput"
              placeholder="Type to Search"
            ></b-form-input>
            <b-input-group-append>
              <b-button :disabled="!filter" @click="filter = ''">
                <img src="@/assets/images/icons/cross.png" alt />
              </b-button>
            </b-input-group-append>
          </b-input-group>
        </b-form-group>
      </b-col>
    </div>
    <div class="container-fluid order-list">
      <!-- START: Card Data-->
      <div class="row">
        <div class="col-12 col-md-12 mt-3 mb-5">
          <div class="card pb-3">
            <div class="p-3 list-filter">
              <div class="btn-group">
                <button
                  type="button"
                  class="btn btn-outline-dark rounded-btn mr-2 mob-mar" :class="{'active' : this.Alltab}" @click="onStatusFilterClicked('All')"
                >{{this.AllText}}</button>
              </div>
              <div class="btn-group">
                <input type="checkbox" id="submitted" name="orderlist" hidden>
                <label for="submitted" type="button" class="btn btn-outline-info rounded-btn mr-2 mob-mar" @click="onStatusFilterClicked('1')">{{this.SubmittedText}}</label>                
              </div>
              <div class="btn-group">
                <input type="checkbox" id="approved" name="orderlist" hidden>
                <label for="approved" type="button" class="btn btn-outline-success rounded-btn mr-2 mob-mar" @click="onStatusFilterClicked('2')">{{this.ApprovedText}}</label>
              </div>
              <div class="btn-group">
                <input type="checkbox" id="rejected" name="orderlist" hidden>
                <label for="rejected" type="button" class="btn btn-outline-danger rounded-btn mr-2 mob-mar" @click="onStatusFilterClicked('3')">{{this.RejectedText}}</label>                
              </div>

              <div class="btn-group">
                <input type="checkbox" id="resubmitted" name="orderlist" hidden>
                <label for="resubmitted" type="button" class="btn btn-outline-warning rounded-btn mr-2 mob-mar" @click="onStatusFilterClicked('4')">{{this.ResubmittedText}}</label>                
              </div>

              <div v-if="canCreateOrder" class="btn-group">
                <button
                  type="button"
                  class="btn btn-info rounded-btn mr-2 mob-mar"
                  @click="createOrder"
                >
                Create Order
                </button>
              </div>
            </div>
            <div class="overflow-auto">
              <b-table
                id="orderList"
                :items="this.getOrders"
                :filter="filter"
                filter-debounce="1000"
                :fields="getfields"
                :current-page="currentPage"
                :per-page="perPage"
                :sort-by.sync="sortBy"
                :sort-desc.sync="sortDesc"
                sort-icon-left
                class="table order"
              >
                <template v-slot:cell(Order_Number)="data">
                  <span>{{ data.item.Order_Number }}</span>
                </template>
                <template v-slot:cell(actionMf)="data">
                  <router-link
                    v-if="data.item.status == 'InConsideration'"
                    :to="{ name: 'OrderPayment', params: {orderId: data.item.orderId} }"
                  >Pay</router-link>
                  <router-link
                    v-else-if="data.item.status == 'Rejected'"
                    :to="{ name: 'Order', params: {orderId: data.item.orderId} }"
                  >Edit</router-link>
                  <router-link
                    v-else-if="data.item.status == 'Submitted'||data.item.status == 'Resubmitted' || data.item.status == 'Fullfilled'"
                    :to="{ name: 'FulFillOrder', params: {orderId: data.item.orderId} }"
                  >View</router-link>
                  <span v-else>{{data.item.status}}</span>
                </template>
                <template v-slot:cell(actionAdmin)="data">
                  <router-link
                    v-if="data.item.status == 'Submitted' ||data.item.status == 'Resubmitted'"
                    :to="{ name: 'OrderReview', params: {orderId: data.item.orderId} }"
                  >Review</router-link>                  
                  <router-link
                    class="ml-3"
                    v-else-if="isNtlUser() && data.item.status == 'InConsideration'"
                    :to="{ name: 'FulFillOrder', params: {orderId: data.item.orderId} }"
                  >Fulfill</router-link> 
                  <router-link
                    class="ml-3"
                    v-else
                    :to="{ name: 'OrderView', params: {orderId: data.item.orderId} }"
                  >View</router-link> 
                </template>
              </b-table>

              <b-pagination
                v-model="currentPage"
                :total-rows=20
                :per-page="perPage"
                class="float-right mr-4"
                first-text="First"
                prev-text="Prev"
                next-text="Next"
                last-text="Last"
              ></b-pagination>
            </div>
          </div>
          <!-- START: Card Data-->
        </div>
      </div>
      <!-- END: Card DATA-->
    </div>
  </div>
</template>

<script>
import axios from "axios";
import roles from "../../router/roles";
export default {
    data() {
        return {
        /*Search */
        filter: "",
        /*Filters*/
        entityIds: [],
        orderStatus: [],
        orderFrom: null,
        orderTill: null,
        locations: [],
        /*Paginations */
        perPage: 10,
        currentPage: 1,
        totalRows: 0,
        sortBy: "requestedOn",
        sortDesc: true,
        AllText: "",
        SubmittedText: "",
        ApprovedText: "",
        RejectedText: "",
        ResubmittedText: "",
        Alltab: true,
        role: "",
        mfFields: [
            {
            key: "orderNumber",
            },
            {
            key: "requestedOn",
            sortable: true,
            },
            {
            key: "status",
            },
            {
            key: "paymentStatus",
            },
            {
            key: "location",
            sortable: true,
            },
            {
            key: "actionMf",
            label: "Actions",
            },
        ],
        adminFields: [
            {
            key: "orderNumber",
            },
            {
            key: "orgName",
            label: "Organization",
            sortable: true,
            },
            {
            key: "requestedOn",
            sortable: true,
            },
            {
            key: "status",
            },
            {
            key: "paymentStatus",
            },
            {
            key: "location",
            sortable: true,
            },
            {
            key: "actionAdmin",
            label: "Actions",
            },
        ],
        };
    },
    props:{'orgType':Number},
    created(){
      this.role=this.$store.state.clientAppDecodedToken.Role
    },
    components: {},
    computed: {
        getfields: function () {
        if (this.canCreateOrder) return this.mfFields;
        return this.adminFields;
        },
        canCreateOrder: function () {
            switch (this.role) {
                case roles.MfAdmin:
                case roles.MfWarehouseIncharge:
                case roles.TpsafAdmin:
                case roles.TpsafFacilityAdmin:
                case roles.TpsafFacilityIncharge:
                return true;
                default:
                return false;
            }
        },
        title: function(){
          if(this.isNtlUser()){
            switch (this.orgType){
              case 3:
                return "Manufacturer Orders";
              case 4:
                return "Stock Orders";
              default:
                return "Orders";
            }
          }
          return "Orders";
        }
    },
    methods: {
        createOrder: function() {
            this.$router.push({ name: 'Order' });
        },
        isNtlUser: function(){
          return this.role == roles.TsspAdmin || this.role == roles.TsspIntermediate || this.role== roles.TsspWarehouseIncharge;
        },
        isTaxAuthUser: function(){
          return this.role == roles.TaxAuthAdmin || this.role == roles.TaxAuthRevenueOfficer;
        },
        onStatusFilterClicked(status){
        if(status=="All")
        {
            this.orderStatus=[];
            this.$root.$emit('bv::refresh::table', 'orderList');
            this.Alltab = true;
            var items=document.getElementsByName('orderlist');
                    for(var i=0; i<items.length; i++){
                        if(items[i].type=='checkbox')
                            items[i].checked=false;
                    }
            return;
        }
        var statusInt = parseInt(status)
        const index = this.orderStatus.indexOf(statusInt);
        if (index > -1) {
            this.orderStatus.splice(index, 1);
            this.Alltab = false;
        }
        else{
            this.orderStatus.push(statusInt);
            this.Alltab = false;
        }
        
        //force refresh on status change
        this.$root.$emit('bv::refresh::table', 'orderList');
        },	
        getOrders: function (ctx) {
            this.role = this.$store.state.clientAppDecodedToken.Role;
            const promise = axios({
                url: "/Orders/GetOrders",
                method: "post",
                baseURL: process.env.VUE_APP_OrderMgmtApisBaseUrl,
                data: {
                    entityIds: this.entityIds,
                    searchText: ctx.filter,
                    locations: this.locations,
                    orderStatuses: this.orderStatus,
                    ordersFrom: this.orderFrom,
                    ordersTill: this.orderTill,
                    sortBy: ctx.sortBy,
                    sortByDesc: ctx.sortDesc,
                    pageNo: ctx.currentPage,
                    pageSize: ctx.perPage,
                    orgType: this.orgType
                }
            });

            // Must return a promise that resolves to an array of items
            return promise.then((response) => {
                // Pluck the array of items off our axios response
                const items = response.data.miniOrderDtos;
                this.totalRows = response.data.totalCount;
                this.AllText = "All(" + response.data.totalCount + ")";
                this.SubmittedText = "Submitted(" + response.data.submittedCount + ")";
                this.ApprovedText = "Approved(" + response.data.approvedCount + ")";
                this.RejectedText = "Rejected(" + response.data.rejectedCount + ")";
                this.ResubmittedText =
                "Resubmitted(" + response.data.resubmittedCount + ")";
                // Must return an array of items or an empty array if an error occurred
                return items || [];
            });
        },
    },    
};
</script>
